import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { AscRate } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-asc-rates',
  templateUrl: './asc-rates.component.html',
  styleUrls: ['./asc-rates.component.scss']
})
export class AscRatesComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<AscRate>;
  displayedColumns: string[] = [
    'lengthFrom',
    'lengthTo',
    'memberOffline',
    'memberOnline',
    'nonMemberOffline',
    'nonMemberOnline',
    'typeOfExecutionObj',
    'typeOfMediumObj',
    'status'
  ];
  statusList = [...STATUSES];

  ascRate = new AscRate();
  ascRateList = [];
  typeOfMediumExecutionList = [];
  typeOfMediumList = [];

  ascRateForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    public validateFieldService: ValidateFieldService,
    private sweetAlertService: SweetAlertService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.ascRates).subscribe((res: any) => {
      this.ascRateList = res.responseData.data;
      this.dataSource = new MatTableDataSource<AscRate>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
    this.getTypeOfMediumList();
  }

  getTypeOfMediumList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfMedium).subscribe((res: any) => {
      this.typeOfMediumList = res.responseData.data;
    });
  }

  setExecution() {
    console.log(this.ascRate.typeOfMedium);
    if (this.ascRate.typeOfMedium) {
      this.apiService.findAll(ENDPOINTS.typeOfExecutionsByMedium + this.ascRate.typeOfMedium.id).subscribe((res: any) => {
        this.typeOfMediumExecutionList = res.responseData.data;
      });
    }
  }

  save(): void {
    console.log(this.ascRate);

    this.apiService.save(ENDPOINTS.ascRates, this.ascRate).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        console.log(err);

        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getAscRate(): void {
    this.apiService.findById(ENDPOINTS.ascRates, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.ascRate = res.responseData.data;
        this.setExecution();
      }
    });
  }

  getTypeOfMediumExecutionList(): void {
    this.apiService.findAll(ENDPOINTS.typeOfExecutions).subscribe((res: any) => {
      this.typeOfMediumExecutionList = res.responseData.data;
    });
  }

  update(): void {

    this.apiService.update(ENDPOINTS.ascRates, this.ascRate).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.ascRate.status = COMMON_FUNCTION.ACTIVE;
    this.ascRateForm = this.formBuilder.group({
      lengthFrom: ['', [Validators.required]],
      lengthTo: ['', [Validators.required]],
      memberOffline: ['', Validators.required],
      memberOnline: [''],
      nonMemberOffline: ['', Validators.required],
      nonMemberOnline: [''],
      typeOfExecution: ['', Validators.required],
      typeOfMedium: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.ascRate = new AscRate();
    this.ascRate.status = COMMON_FUNCTION.ACTIVE;
    this.ascRateForm.markAsUntouched();
    this.ascRateForm.markAsPristine();
  }

  setPenalty(penaltyParam): boolean {
    return penaltyParam === 'Yes' ? true : false;
  }

  compareMediumExecution(value: any, selected: any): boolean {
    if (value && selected) {
      return value.typeOfExecution === selected.typeOfExecution;
    }
  }

  compareTypeOfMedium(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this ASC rate?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.ascRates, this.ascRate.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('ASC rate successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
